*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --background-main-color: #4f2ee8;
  --background-dar-color: #202024;
  --background-light-color: #f6f6f6;
  --background-active-color: #eac645;
  --main-color: #111;
  --main-white-color: #fff;
  --text-color-categories: rgba(17, 17, 17, .6);
  --scrollbar-color: #e0e0e0;
  --duration-time: 250ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

:root[ui-theme="dark"] {
  --background-main-color: #4f2ee8;
  --background-dar-color: #f6f6f6;
  --background-light-color: #202024;
  --background-active-color: #eac645;
  --main-color: #fff;
  --main-white-color: #111;
  --text-color-categories: rgba(255, 255, 255, .6);
  --scrollbar-color: rgba(17, 17, 17, .6);
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, h1, h2, h3, h4, p {
  margin: 0;
}

body {
  background-color: var(--background-light-color);
  color: var(--main-color);
  letter-spacing: -.04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.76;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  border-color: var(--background-main-color);
  cursor: pointer;
  border-radius: 40px;
  font-weight: 700;
}

button:hover {
  background-color: var(--background-main-color);
}

button:focus {
  background-color: var(--background-light-color);
  border-color: 2px solid var(--background-main-color);
  color: var(--main-white-color);
  box-shadow: 0 4px 18px rgba(0, 0, 0, .15);
}

button:disabled {
  border-color: var(--btn-default-color);
}

.backdrop {
  width: 100%;
  height: 100%;
  transition: opacity var(--duration-time) var(--timing-function), visibility var(--duration-time) var(--timing-function);
  z-index: 999;
  background: rgba(17, 17, 17, .4);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.header {
  background: var(--main-white-color);
  border: 1.5px solid var(--main-color);
  min-width: 320px;
  border-radius: 0 0 8px 8px;
  margin-left: auto;
  margin-right: auto;
}

a {
  height: 28px;
}

.container {
  height: 68px;
  min-width: 320px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0 32px;
  display: flex;
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav-list li {
  display: inline-block;
}

.header-home:after {
  background-color: var(--background-active-color);
  border-radius: 36px;
  outline: none;
}

.header-burger {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 0;
}

.header-burger:hover {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
}

.theme-switch {
  justify-content: center;
  align-items: center;
  display: flex;
}

#switch {
  cursor: pointer;
  margin-right: 14px;
  padding-top: 15px;
}

input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: linear-gradient(180deg, var(--background-main-color) 0%, #dcdcdc 100%);
  border-radius: 20px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: var(--main-white-color);
  border-radius: 10px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + label {
  background: linear-gradient(180deg, var(--background-main-color) 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

label:active:after {
  width: 38px;
}

.header-home {
  height: 34px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  padding: 8px 16px;
}

.active {
  width: 72px;
  height: 34px;
  background: var(--background-active-color);
  border-radius: 36px;
  font-weight: 700;
}

.active-shop {
  width: 156px;
  height: 34px;
  background: var(--background-active-color);
  border-radius: 36px;
  font-weight: 700;
}

.header-shop:hover, .header-shop:focus {
  background-color: var(--background-active-color);
  border-radius: 36px;
  outline: none;
}

.header-burger-icon {
  width: 28px;
  height: 28px;
  stroke: var(--main-color);
}

.header-burger-icon:hover {
  stroke: var(--background-main-color);
}

